import React from 'react';
import { Navbar, Nav } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';
import './Navbar.css';

function Navigation() {
  return (
    <Navbar bg="dark" variant="dark" expand="lg" className="mb-3" fixed="top">
      <Navbar.Brand as={NavLink} to="/" className="navbar-brand">Bloodborne</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link as={NavLink} to="/" exact activeStyle={{ color: 'red' }}>Walkthrough</Nav.Link>
          <Nav.Link as={NavLink} to="/checklist" activeStyle={{ color: 'red' }}>Checklist</Nav.Link>
          <Nav.Link as={NavLink} to="/faq" activeStyle={{ color: 'red' }}>FAQ</Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
}

export default Navigation;