import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';

function Checklist({ tasks = [], onTaskListChange = () => {}, areaName }) {
  const [taskList, setTaskList] = useState(tasks);

  useEffect(() => {
    onTaskListChange(taskList);
  }, [taskList, onTaskListChange]);

  const handleTaskClick = (event, taskId) => {
    // Prevent the event from bubbling up to the hyperlink
    event.stopPropagation();

    setTaskList(taskList.map(task => 
      task.id === taskId ? { ...task, completed: !task.completed } : task
    ));
  };

  // Calculate the total number of tasks and the number of completed tasks
  const totalTasks = taskList.length;
  const completedTasks = taskList.filter(task => task.completed).length;

  return (
    <div className="checklist">
      <h2>{areaName} - {completedTasks}/{totalTasks}</h2>
      <ul style={{ margin: 0, padding: 0 }}>
        {taskList.map(task => (
          <li key={task.id} style={{ display: 'flex', alignItems: 'center', margin: 0, padding: 0 }}>
            <input id={`task-${task.id}`} type="checkbox" checked={task.completed} onChange={(event) => handleTaskClick(event, task.id)} style={{ marginRight: '10px' }} />
            <label htmlFor={`task-${task.id}`} style={{ display: 'flex', alignItems: 'center', width: '100%', outline: 'none' }}>
              <span style={{ textDecoration: task.completed ? 'line-through' : 'none' }}>
                <ReactMarkdown>{task.name}</ReactMarkdown>
              </span>
            </label>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Checklist;